import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { calcTotalPrice } from '../../../utils/calcTotalPrice';
import { getCartFromLS } from '../../../utils/getCartFromLS';
import { CartItem, CartSliceState } from './types';

const { items, totalPrice } = getCartFromLS();      // localStorage

const initialState: CartSliceState = {
  totalPrice,
  items,
};

const cartSlice = createSlice({
  name: 'cart',
  initialState,
  reducers: {
    addItem(state, action: PayloadAction<CartItem>) {
      const findItem = state.items.find((obj) => obj.id === action.payload.id); //     ->

      if (findItem) {
        findItem.count++; // ->       
      } else {
        state.items.push({
          //         
          ...action.payload, //      ->
          count: 1, // ->     count 1         1 
        });
      }

      state.totalPrice = calcTotalPrice(state.items);
    },
    minusItem(state, action: PayloadAction<string>) {
      const findItem = state.items.find((obj) => obj.id === action.payload);

      if (findItem) {
        findItem.count--;

        if (findItem.count === 0) {
          // const index = state.items.findIndex((obj) => obj.id === action.payload);    //      
          // state.items.splice(index, 1);                          //       

          // this.removeItem(state, { payload: findItem.id });                   //     1  
        }
      }

      state.totalPrice = calcTotalPrice(state.items);
    },
    removeItem(state, action: PayloadAction<string>) {
      // state.items = state.items.filter((obj) => obj.id !== action.payload);             //     
      console.log('remove item');
      const findItem = state.items.findIndex((obj) => obj.id === action.payload); //      
      state.items.splice(findItem, 1); //    
    },
    clearItems(state) {
      state.items = [];
      state.totalPrice = 0;                     //2   

      // return initialState;
    },
  },
});

export const { addItem, removeItem, minusItem, clearItems } = cartSlice.actions;

export default cartSlice.reducer;
